*{
  margin: 0;
  padding: 0;
  border: none;
}

body{
  background-image: url('https://cdn.pixabay.com/photo/2016/03/24/13/45/coffee-1276778_960_720.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.container{
  height: 100vh;
  width: 75vw;
  margin: auto;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.5rem;
  border: 1px solid #c0bcbb25;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.301);

}

section{
  padding-top: 20px;
}

h1{
  font-size: 2rem;
  text-align: center;
}

article{
  padding: 15px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

input{
  width: 50%;
  height: 50px;
  font-size: 1.5rem;
  word-wrap: normal;
  border: 1px solid #78655F;
  border-radius: 10px;
  background: inherit;
  padding-left: 8px;
}

button{
  width: 50px;
  height: 50px;
  background: inherit;
  cursor: pointer;
  border-radius: 8px;
  font-size: 1.8rem;
}

button:hover{
  background-color: rgba(0, 0, 0, 0.1);
}

.plans > p{
  word-break: break-all;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.plans{
  padding: 0 15px;
}
